

.vkuiEpic #game .vkuiSnackbar {
  padding-bottom: var(--safe-area-inset-bottom);
}

.vkuiPanelHeader__content-in {
  font-weight: 700;
}

.vkuiEpic .vkuiFixedLayout--bottom.fixed--promo{
  padding-bottom: 0;
}
.Icons__helth{
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.Word__container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.Word__letter {
width: 40px;
height: 40px;
color: var(--button_primary_background);
/* background-color: var(--button_secondary_background_highlighted); */
box-sizing: border-box;
margin: 3px;
border-radius: 4px;

border: solid 0.5px var(--header_tab_inactive_text);
/* --placeholder_icon_foreground_secondary */

display: flex;
font-size: 22px;
justify-content: center;
align-items: center;
}
  



/* Profile */
.profile--gradient{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
}

.profile--title_user_name{
  margin-bottom: 8px;
  margin-top: 20px; 
}
.profile--text_user_rank{
  color: var(--text_secondary)
}





