.keybord {
  box-shadow: 0px -7px 20px 2px rgb(0 0 0 / 6%), inset 0 0 5px 0 rgb(0 0 0 / 8%);
  /* box-shadow: 0px -7px 20px 20px rgb(0 0 0 / 6%), inset 0px 20px 20px 3px rgb(0 0 0 / 8%); */
  position: fixed;
  bottom: 0;
  padding-bottom: calc(var(--tabbar_height) + var(--safe-area-inset-bottom));
  left: 0;
  right: 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  
  display: flex;
  flex-direction: column;
  align-items: center;
}

.letters {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 15px;
}
.letter {
  background-color: var(---background_content);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  line-height: 1;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  color: var(--button_primary_background);
  /* color: #132f63; */
}

.iconLetter{
  position: absolute;
  bottom: 0;
  right: 0;
  
  background-color: #ffffff33;
  border-radius: 50%;
}


.Counter__hints{
  /* background: #aeb7c24a; */
  /* background: var(--background_highlighted);
  color: var(--button_primary_background); */
  position: absolute;
  top: -10px;
  right: -20px;
  pointer-events: none;
  min-width: 34px;
}
