.SliderSwitchIcons {
    display: flex;
    position: relative;
    width: 120px;
    height: 60px;
    border-radius: 50px;
    box-sizing: border-box;
    background-color: var(--field_background);
    transition: all 100ms ease-out;
    outline: none;
  }
  
  .SliderSwitchIcons__button {
    width: 50%;
    height: 100%;
    margin: 0;
    padding: 0;
    border: 0;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    cursor: pointer;
    background: transparent;
    transition: color 100ms ease-out;
    outline: none;
    user-select: none;
    z-index: 1;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
  }
  
  
  
  /* .SliderSwitchIcons__button--hover {
     transition: transform 150ms ease-in; 
  } 
  */
  
  .SliderSwitchIcons__slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    margin: 0px;
    border-radius: 50%;
    transition: transform 150ms;

    box-shadow: 0px 5px 19px 1px rgb(0 0 0 / 18%);
  border: 0.3px solid var(--tabbar_active_icon);
  box-sizing: border-box;
  }
  
  .SliderSwitchIcons--firstActive {
    background-color: var(--background_content);
  }
  
  .SliderSwitchIcons--secondActive {
    transform: translateX(100%);
    background-color: var(--background_content);
  }